import React from "react";
import Women from "../pictures/Women.png";
import { JwModal } from "./JwModal";
import { withRouter } from "react-router-dom";
import MenuIcon from "@material-ui/icons/Menu";
class TopMenu extends React.Component {
  render() {
    return (
      <>
        <div className="top-menu">
          <img src={Women} alt="Women's beauty" />
          <span onClick={JwModal.open("custom-modal-2")}>MAKE UP</span>
          <span>SKIN CARE</span>
          <span onClick={() => this.props.history.push("/products")}>HAIR</span>
          <span>Tutorials</span>
          <span>VIRTUAL TRY ON</span>
          <span>BEAUTY MAGAZINE</span>
          <p
            className="menu"
            onClick={() => this.props.history.push("/Products")}
          >
            <MenuIcon />
          </p>
        </div>
        <JwModal id="custom-modal-2">
          <h1 style={{ height: 500 }}>A Tall Custom Modal!</h1>
          <button onClick={JwModal.close("custom-modal-2")}>Close</button>
        </JwModal>
      </>
    );
  }
}
export default withRouter(TopMenu);
